/* Nosotros */

@use '../base/variables' as v;
@use '../base/mixins' as m;

.nosotros__contenido{
    @include m.tablet{ 
        @include m.grid(2, 2rem);
    } 

    .nosotros__imagen{
        margin-bottom: 5rem;
        @include m.tablet{ 
            margin-right: 2rem;
            margin-bottom: 0;
        }
        img{
            transform: rotate(-6deg);
            border: 3rem solid v.$blanco;
            box-shadow: 9px 10px 5px -7px rgb(0 0 0 /.35);
        }
    }

    .nosotros__texto{
        p{
            &:first-of-type{
                text-align: right;
            }
            
           &:last-of-type{
            text-align: left;
            }
            @include m.tablet{ 
                &:first-of-type{
                    text-align: justify;
                }
                
               &:last-of-type{
                text-align: justify;
                }
            }
        } 
        blockquote{
            margin: 0;
            padding: 1rem;
            border-left: .5rem solid v.$primario;
            font-size: 2rem;
        }
    }

}